.video {
    position: relative;
    max-height: 60rem; 
    overflow: hidden;//importante para dar scroll a un contenedor que se desborda, esconderlo etc
    .overlay {
        position: absolute; //al colocar este absoluta y el padre relativa este va a usar al padre como contendor y no se va a salir de su cordenadas
        width: 100%;  
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e3792+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%, rgba(158,55,146,0.6) 67%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,146,0.6) 67%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,146,0.6) 67%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    }
    video {
        display: block;
        width: 100%;
    }
}
.contenido-video {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;// para poder seleccionar el texto es necesario indicar su posicion relativa y definir index z 1 para que se ponga adelante del video 
    z-index: 1 ;

    h2 {
        display: flex;
        flex-direction: column;//esto se hace para darle el salto de linea al span
        align-items: flex-start; //forza a contenedor rosa a pegarse al span y no tomar todo el espacio del h2
        color: $blanco;
        font-size: 7rem;

        span {
            background-color: $rosa;
            font-size: 2rem;
            padding: 1rem 2rem;
            border-radius: 1rem;
        }
    }
}