
.header{
    background-color: $verde;

    // en este caso .header .contenedor-header se usan asi porque en la anidacion son clase padre y hijo
    .contenedor-header {
        padding: $separacion/2 0; //5rem arriba y abajo y 0 derecha izquierda
        @include tablet {
            display: flex;
        justify-content: space-between; // divide los elementos entre ellos (h1-enlaces)
        align-items: center; // coloca los enlaces al centro del contenedor
        }
    }
    h1 {
        color: $blanco;
        margin-bottom: 1rem;
        text-align:center;
        @include tablet {
            margin-bottom: 0;
        }
    }
    //en este caso es &.fijo ya que estan al mismo nivel no son padre e hijo deberia ser .header .fijo pero para evitar redundancias se usa &
    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .4rem #444; //esto es para darle sombre al nav cuando esta fijo
    }
}

.navegacion-principal {
    display: grid;   //esto es para separar los enlaces 2 rem
    grid-template-columns: repeat(3,1fr);
    column-gap: 2rem; // puede ser reemplazado todo solo con gap: 2rem
    justify-content: center;
    
    a {
        font-weight: $regular;
        color: $blanco;
        font-size: $separacion/2.5;
        text-align: center;
        &:hover {
            color: $amarillo; //& cuando se coloca anidado dentro de un selector hace referencia al padre para que no se repita y de error el codigo al compilar 
        }
    }
}