.lineup {
    background-color: $rosa;
    padding: $separacion 0;

    h3, p {
        text-align: center;
        color: $blanco;
    }
    .dia {
        text-transform: uppercase;
        margin: $separacion;
    }
}
.escenarios-contenedor {
    @include tablet {
        @include grid(2, $separacion)
    }
    .escenario {
        padding-top: $separacion/2;
    }
    .nombre-escenario {
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &:after {
            content: ""; //sin esto no funciona por ser un seudo elemento
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            // background-color: aquamarine; esto es para ver donde esta localicado pero no se usa en el css
            background-size: 15rem; // para que se pueda ver la imagen hay que defir el tamaño que tendra dentro del contenedor
            background-repeat: no-repeat; // para evitar que se repita la imagen
            background-position: center center; // centra horizontal y verticalmente la imagen
            

        }
    } 
}
.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);//cuando agregar una imagen con background image por default se van a repetir

        
    }
}
.rock .nombre-escenario {
    &:after {
        background-image: url(../img/icono_rock.png);
        
        
    }
}
.calendario {
    padding: 0; // para pegar los elementos de la lista al contenedor
    list-style: none;//para quitar las viñetas de ls ul
    li {
        padding: $separacion/2;
        font-weight: $bold;
        span {
            font-weight: $regular;
        }
    }
}
.bg-verde .calendario li:nth-child(odd) {//:nth-child()sirve para seleccionar elementos en listas de elementos por medio de una secuencia numerica o una guia como en este caso seleccione a los elementos impares(odd)
    background-color: darken($verde, 10);// darken es una funcion de sass que al color que le pase de la un porcentaje de tonalidad de negro en este caso 10% mas obscuro para diferenciar el color //li
}
.bg-amarillo .calendario li:nth-child(odd) {
    background-color: darken($amarillo, 10);//como existe darken tambien existe lighten que usa blanco en porcentaje para aclarar el fondo
}